/* Apply the Michroma font globally */
@import url('https://fonts.googleapis.com/css2?family=Michroma&display=swap');

.salvage-page {
  margin: 0;
  padding: 0;
  position: relative; /* For pseudo-element overlays */
  height: auto; /* Adjusted height to fit content */
  background-image: url('/public/salvage-kraken.jpg'); /* Background image */
  background-size: cover; /* Image covers the entire container */
  background-position: center; /* Centers the image */
  background-attachment: fixed; /* Keeps it fixed on scroll */
  background-repeat: no-repeat; /* Prevents tiling */
  z-index: 1;
}

.salvage-page::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.3); /* Slight dark overlay */
  z-index: -1; /* Keeps it behind other content */
}

/* Mission table styling - Adjusted column widths for better alignment */
.salvage .header-row,
.salvage .mission-row {
  display: grid;
  grid-template-columns: 1.5fr 1.2fr 1.2fr 1.2fr 1.2fr 1.5fr; /* Adjusted columns to provide more space for inputs */
  align-items: center;
  gap: 5px; /* Increased gap to make fields less squished */
  text-align: center;
  font-weight: bold;
  margin-bottom: 10px;
  font-family: 'Michroma', sans-serif;
}

.salvage .header-item1 {
  color: #ff0000; /* Main text color */
  padding: 5px;
  font-size: 20px;
  font-weight: bold; /* Use bold to emphasize */
  text-shadow: 
    1px 1px 0 #000000,  /* Bottom right */
    -1px 1px 0 #000000, /* Bottom left */
    1px -1px 0 #000000, /* Top right */
    -1px -1px 0 #000000; /* Top left */
}

.salvage .header-item2 { 
  color: #00bfff; /* Blue Sale Amount title */
  padding: 5px;
  font-size: 20px;
  font-weight: bold; /* Keep bold for emphasis */
  text-shadow: 
    1px 1px 0 #000000,  /* Bottom right */
    -1px 1px 0 #000000, /* Bottom left */
    1px -1px 0 #000000, /* Top right */
    -1px -1px 0 #000000; /* Top left */
}

/* Mission row span colors for alignment with header */
.salvage .mission-row span:nth-child(2),
.salvage .mission-row span:nth-child(3),
.salvage .mission-row span:nth-child(4),
.salvage .mission-row span:nth-child(5) {
  color: #ff0000; /* Red for the expenses columns values */
}

.salvage .mission-row span:nth-child(6) {
  color: #32CD32; /* Green for Sale Amount values */
}

/* Input fields within mission-row to avoid squishing */
.salvage .mission-row input[type="number"] {
  min-width: 100px; /* Minimum width to ensure visibility */
  width: 100%; /* Full width to fill available grid space */
  padding: 8px;
  box-sizing: border-box; /* Include padding in width calculation */
}

/* Mission Labels (e.g., Mission 1, Mission 2, etc.) */
.salvage .mission-row span:first-child {
  color: rgb(255, 255, 255); /* White text color */
  padding: 5px;
  font-size: 20px;
  font-weight: bold;
  text-shadow: 
    1px 1px 0 #000000,  /* Bottom right */
    -1px 1px 0 #000000, /* Bottom left */
    1px -1px 0 #000000, /* Top right */
    -1px -1px 0 #000000; /* Top left */
}

/* Crew section - Adjusted for better spacing */
.salvage .crew-row {
  display: grid;
  grid-template-columns: 2fr 1fr 1fr 1fr 1fr; /* Define columns */
  grid-template-rows: auto auto; /* Add two rows for stacking */
  gap: 10px;
  align-items: center; /* Center align other rows */
}

.salvage .crew-expenses-container {
  display: flex;
  flex-direction: column; /* Stack inputs vertically */
  justify-content: center; /* Align vertically within its grid cell */
  align-items: center; /* Center the inputs horizontally */
  margin-top: 10px; /* Add spacing to push it down */
}

.salvage .crew-add-expenses {
  grid-column: 2; /* Place it under the Expenses column */
  grid-row: 2; /* Place it in the second row */
  justify-self: start; /* Optional: Align left */
  margin-top: -8px; /* No space between expenses and add expenses */
  margin-bottom: 20px; /* Add spacing between the crew sections */
}

.salvage .crew-name,
.salvage .crew-expenses,
.salvage .toggle-container,
.salvage .crew-time,
.salvage .crew-payout {
  text-align: center;
  box-sizing: border-box;
  margin: 0;
  font-family: 'Michroma', sans-serif;
}

.salvage .crew-time {
  color: #ffffff; /* Golden color for Time and Payout labels */
  padding: 5px;
  font-size: 18px;
  font-weight: bold;
  text-shadow: 
    1px 1px 0 #000000,  /* Bottom right */
    -1px 1px 0 #000000, /* Bottom left */
    1px -1px 0 #000000, /* Top right */
    -1px -1px 0 #000000; /* Top left */
}

.salvage .crew-payout {
  color: #3bff3b; /* Green color for Payout label */
  padding: 5px;
  font-size: 18px;
  font-weight: bold;
  text-shadow: 
    1px 1px 0 #000000,  /* Bottom right */
    -1px 1px 0 #000000, /* Bottom left */
    1px -1px 0 #000000, /* Top right */
    -1px -1px 0 #000000; /* Top left */
}

.salvage .crew-expenses {
  margin-top: 0; /* Reset margin to avoid misalignment */
}

/* Crew input fields within crew-row */
.salvage .crew-row input[type="text"],
.salvage .crew-row input[type="number"] {
  min-width: 100px; /* Prevent fields from getting squished */
  width: 100%; /* Fill the grid column */
  padding: 8px;
  box-sizing: border-box; /* Include padding and border in width calculation */
}

/* Ensure inputs are not squished by setting a reasonable fixed width */
.salvage input[type="number"],
.salvage input[type="text"] {
  font-family: 'Michroma', sans-serif;
  font-size: 16px;
  padding: 10px;
  background-color: #333;
  color: white;
  border: 1px solid #888;
  border-radius: 5px;
  text-align: center;
  min-width: 100px; /* Ensure fields are not too small */
  box-sizing: border-box;
}

/* Greyed-out placeholder styling for crew expenses */
.salvage .crew-expenses::placeholder {
  color: #7a7a7a; /* Light grey color */
}

/* Mission controls container */
.salvage .mission-controls {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 15px; /* Space between each button group for vertical alignment */
  margin-top: 20px; /* Overall top margin for the container */
}

/* Buttons for add/remove mission */
.salvage .mission-buttons {
  display: flex;
  justify-content: center;
  gap: 5px; /* Restored original space between Add and Remove buttons */
  margin-left: 0px; /* Adjusted left margin for better alignment */
  margin-top: 0px;
  margin-bottom: -10px;
}

/* Button for Clear Mission Data */
.salvage .clear-mission-button {
  text-align: center;
  margin-top: -12px; /* Adjusted top margin for better alignment */
  margin-bottom: 100px; /* Adjusted bottom margin for better spacing */
}

.salvage .luminis-logo {
  position: relative; /* Allows positioning for child elements */
  z-index: -1; /* Ensures the logo is above other elements */
  display: flex;
  justify-content: center; /* Center the logo horizontally */
  align-items: center;
  margin: 0; /* Remove extra margin */
}

.salvage .mission-logo {
  position: absolute; /* Position the image behind other content */
  z-index: -1; /* Pushes the image behind other elements */
  width: 750px; /* Adjust size as needed */
  height: auto; /* Maintain aspect ratio */
  opacity: 0.9; /* Make it slightly transparent for better visibility of buttons */
  margin-top: 150px; /* Adjusted top margin for better alignment */
}

.salvage .mission-logo:hover {
  transform: scale(1.1); /* Add a slight zoom effect on hover */
  opacity: 1; /* Make it fully visible */
}

/* Start/Stop buttons side by side */
.salvage .start-stop-buttons {
  display: flex;
  justify-content: center;
  gap: 5px; /* Restored original spacing */
  margin-top: 175px;
  margin-bottom: 0px;
}

/* Reset Mission Clock button */
.salvage .reset-clock-button {
  text-align: center;
  margin-top: -12px;
  margin-bottom: 10px;
}

/* Master Clock styling */
.salvage .master-clock {
  color: #ffffff; /* White text color */
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px; /* Space between clock elements */
  margin-top: -20px; /* Adjusted top margin for better alignment */
  margin-bottom: 10px; /* Adjusted bottom margin for better spacing */
  font-size: 20px; /* Adjusted font size for better visibility */
  font-family: 'Michroma', sans-serif;
}

.salvage .small-auec {
  font-size: 0.65em; /* Shrinks the size to 75% of the parent */
  opacity: 0.7;     /* Makes it slightly transparent */
  margin-left: 3px; /* Adds a bit of spacing */
}

.salvage .crew-add-expenses {
  min-width: 100px;
  width: 100%;
  padding: 8px;
  font-family: 'Michroma', sans-serif;
  font-size: 16px;
  background-color: #333;
  color: white;
  border: 1px solid #888;
