/* Buttons for Add/Remove Crew */
.salvage .crew-buttons {
  display: flex;
  justify-content: center;
  gap: 5px; /* Restored original spacing */
  margin-top: 0px;
  margin-bottom: 5px;
}

/* Button for Clear Crew Data */
.salvage .clear-crew-button {
  text-align: center;
  margin-top: 5px;
  margin-bottom: 450px; /* Adjusted bottom margin so the container fills the page */
}

/* All other existing CSS styles remain unchanged below */
.salvage .salvage {
  display: flex;
  background-color: #ff0000;
  color: rgb(255, 0, 0);
  padding: 20px;
  height: 100vh;
  overflow-x: hidden;
  overflow-y: hidden;
}

