/* Clear buttons - Standout reddish styling */
.salvage .clear-button {
  background-color: #af0000;
  color: white;
  border: 2px solid #888;
  padding: 10px 20px;
  font-size: 18px;
  cursor: pointer;
  border-radius: 5px;
  font-family: 'Michroma', sans-serif;
  transition: background-color 0.3s ease, transform 0.3s ease;
  margin: 0;
  width: 275px; /* Set a fixed width for consistent button sizes */
  text-align: center; /* Center text within the button */
  box-sizing: border-box; /* Include padding and border in width calculation */
}

.salvage .clear-button:hover {
  background-color: #ff1a1a; /* Slightly darker red on hover */
  transform: scale(1.05); /* Slight pop effect */
  box-shadow: 0 6px 12px rgba(255, 0, 0, 0.7);
}

.salvage .clear-button:active {
  transform: scale(0.95); /* Slight shrink on click */
}

.salvage .custom-button {
  background-color: #3f3f3f;
  color: white;
  border: 2px solid #888;
  padding: 10px 20px;
  font-size: 18px;
  cursor: pointer;
  border-radius: 5px;
  font-family: 'Michroma', sans-serif;
  transition: background-color 0.3s ease, transform 0.3s ease;
  margin: 0; /* Removed all automatic margins to ensure spacing is handled manually via containers */
  width: 250px; /* Set a fixed width for consistent button sizes */
  text-align: center; /* Center text within the button */
  box-sizing: border-box; /* Include padding and border in width calculation */
}

.salvage .custom-button:hover {
  background-color: #555;
  transform: scale(1.05); /* Slight pop effect */
  box-shadow: 0 6px 12px rgba(168, 168, 168, 0.7);
}

.salvage .custom-button:active {
  background-color: #666;
  transform: scale(1);
}

.salvage .button-container {
  display: flex;
  justify-content: space-evenly;
  margin-top: 30px;
}

.salvage .captain-controls {
  text-align: center;
  margin-top: 100px;
  display: flex;
  flex-direction: column;
  gap: 10px;
  align-items: center;
}

.salvage .captain-button-row {
  display: flex;
  gap: 10px; /* Restored original gap for better button spacing */
}

/* Expenses Placeholder: Grey */
.salvage .crew-expenses::placeholder {
  color: rgba(255, 255, 255, 0.5); /* Light grey color */
  opacity: 1; /* Ensures full visibility */
}

/* Captain Placeholder: White */
.salvage .captain-placeholder::placeholder {
  color: rgb(255, 255, 255) !important;
  opacity: 1; /* Ensures full visibility */
}

/* Crew Placeholder: Grey */
.salvage .crew-placeholder::placeholder {
  color: rgba(255, 255, 255, 0.5); /* Light grey color */
  opacity: 1;
}

@media (max-width: 768px) {
  .salvage .sidebar {
    flex-basis: 100%;
    height: auto;
    margin-bottom: 20px;
  }

  .salvage .table-container {
    flex-basis: 100%;
  }

  .salvage .custom-button {
    padding: 8px 16px;
    font-size: 16px;
  }
}

/* Toggle Switch Container */
.salvage .toggle-container {
  display: flex;
  align-items: center;
  gap: 10px;
  font-family: 'Michroma', sans-serif;
}

/* Toggle Label Styling */
.salvage .toggle-label {
  font-size: 14px;
  font-weight: bold;
  color: #888;
  text-transform: uppercase;
}

.salvage .highlight-out {
  color: #ff4d4d; /* Red for "Out" */
}

.salvage .highlight-in {
  color: #32cd32; /* Green for "In" */
}

/* Toggle Switch Base */
.salvage .toggle-switch {
  width: 60px;
  height: 25px;
  background-color: #ddd;
  border-radius: 20px;
  position: relative;
  cursor: pointer;
  transition: background-color 0.3s ease;
  box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.3);
}

.salvage .toggle-switch.in {
  background-color: #32cd32; /* Green when "IN" is active */
}

.salvage .toggle-switch.out {
  background-color: #ff4d4d; /* Red when "OUT" is active */
}

/* Toggle Button (Slider) */
.salvage .toggle-button {
  width: 22px;
  height: 22px;
  background-color: white;
  border-radius: 50%;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 2px;
  transition: left 0.3s ease;
}

.salvage .toggle-switch.in .toggle-button {
  left: 36px; /* Move to the right when "IN" is active */
}
