@import url('https://fonts.googleapis.com/css2?family=Michroma&display=swap');

.michroma-regular {
  font-family: 'Michroma', serif;
  font-weight: 400;
  font-style: normal;
}

.Homepage {
  text-align: center;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  background-image: url('/public/krakenhomepage.jpg');
  background-size: cover;
  background-position: center;
}

.Homepage-header {
  background-color: #121213;
  padding: 20px;
  color: white;
}

.Homepage-main {
  flex-grow: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px;
  background-color: #f0f0f000;
}

.Homepage-links {
  display: flex;
  justify-content: center; /* Center the buttons */
  gap: 20px;             /* Add space between buttons */
  width: 100%;
  max-width: 600px;
  margin: 0 auto;        /* Center the container itself */
}

/* Updated Homepage-link to ensure equal size and centered content */
.Homepage-link {
  width: 250px;           /* Fixed width for all buttons */
  color: #61dafb;
  text-decoration: none;
  font-size: 1.2em;
  padding: 10px 20px;
  border: 2px solid #61dafb;
  border-radius: 5px;
  transition: background-color 0.3s, color 0.3s;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center; /* Center the text vertically */
}

.Homepage-link:hover {
  background-color: #61dafb;
  color: #121213;
}

/* Salvage button specific styles */
.salvage-title {
  font-size: 30px; /* Larger font for "Salvage" */
  font-weight: bold;
}

.salvage-subtitle {
  font-size: 14px; /* Smaller font for subtitle */
  color: #9e9e9e;
}

/* Mining button specific styles */
.mining-title {
  font-size: 30px;
  font-weight: bold;
}

.mining-subtitle {
  font-size: 14px;
  color: #9e9e9e;
}

/* Hide the hover text by default for the mining button */
.mining-subtitle .hover {
  display: none;
  color: rgb(255, 0, 0);
  font-weight: bold;
  text-shadow: 0 0 3px #000;
}

/* On hover, hide the default text and show the hover text */
.mining-button:hover .mining-subtitle .default {
  display: none;
}

.mining-button:hover .mining-subtitle .hover {
  display: inline; /* Or block, if you prefer */
}

.Homepage-footer {
  background-color: #121213;
  padding: 10px;
  color: white;
}

nav ul {
  list-style-type: none;
  padding: 0;
}

nav ul li {
  display: inline;
  margin: 0 10px;
}

nav ul li a {
  color: #61dafb;
  text-decoration: none;
}

nav ul li a:hover {
  text-decoration: underline;
}